*{
    color: black;
    box-sizing: border-box;
}

body{
    font-family: 'Nunito Sans', sans-serif;
}

.padding-x{
    padding: 0 100px;
}

a{
    text-decoration: none;
}

@media (max-width: 700px) {
    .padding-x {
        padding: 0 30px;
    }
}
@media (max-width: 500px) {
    .padding-x {
        padding: 0 20px;
    }
    body {
        font-size: 1rem;
    }
}

select {
    background-color: white;
    padding: 8px;
    width: 500px;
}

.search-container {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.search-container input{
    width: 400px;
    height: 40px;
}
@media (max-width:830px) {
    .search-container input{
        margin-bottom: 20px;
    }
}
.search-container select{
    width: 200px;
    height: 40px;
}

.countries-container{
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
}

.country-link{
    height: 300px;
    width: 230px;
    margin: 30px 20px;
    box-shadow: 1px 1px 12px -5px rgb(212, 212, 212);
}
@media (max-width:500px) {
    .country-link{
        width: 270px;
        height: 330px;
    }
}
.country{
    width: 100%;
    height: 100%;
}

.country img{
    height: 50%;
    width: 100%;
    object-fit: cover;
}

.country-name{
    font-weight: 800;
    font-size: 1.1rem;
    text-decoration: none;
    margin-bottom: 10px;

}

.padding-left{
    padding-left: 20px;
}
.margin-0{
    margin: 0;
}

/* Country page */

button {
    padding: 5px 40px;
    margin-bottom: 35px;
}

.main-container{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.flex-container{
    width: 47%;
}

.country-flag {
    width: 100%;
}

.country-details-container {
    font-size: 0.88rem;
    display: flex;
    justify-content: space-between;
}
.country-details-container span {
    font-weight: 600;
}
.country-details-container p{
    margin: 5px 0;
}

.border-container{
    font-size: 0.88rem;
}
.border-container span {
    font-weight: 600;
}

.border {
    display: inline-block;
    padding: 2px 10px;
    border: 1px solid rgb(173, 173, 173);
    border-radius: 3px;
    margin: 2px 5px;
}